/**
 * Simple Reset
 */
*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
  line-height: 1.2;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
}

.alert-dismissible {
  transition-duration: 0.75s;
}
.alert-dismissible.dismiss {
  transform: translateX(-200%);
  opacity: 0;
}

template,
[hidden] {
  display: none !important;
}